import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewConstructionStageComponent } from './view-construction-stage.component';

const routes: Routes = [
  {
    path: '',
    component: ViewConstructionStageComponent,
    data: {
      title: 'View Equipment'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViewConstructionStageRoutingModule {}



